  color: #c7b299; }

.projects .card-text {
  color: #d1d1d1; }

.projects .card {
  background-color: #fbfaf8;
  border: none; }
  .projects .card:hover, .projects .card:focus {
    background-color: #362f2d;
    transition: 0.7s; }
