    .projects .card:hover .card-img-overlay, .projects .card:focus .card-img-overlay {
      visibility: visible;
      opacity: 1; }

.projects .card-body {
  position: relative; }
  .projects .card-body::before {
    content: "";
    position: absolute;
    top: -29px;
    left: 20px;
    width: 0;
    height: 0;
    border: 15px solid #fbfaf8;
    border-left-color: transparent;
    border-right-color: transparent;
    border-top-color: transparent; }

.card__img-wrapper {
