.card__tools-zoom {
  padding: 0; }
  .card__tools-zoom:hover svg, .card__tools-zoom:focus svg {
    fill: #c7b299; }

/* ---------------------------------- Promo --------------------------------- */
.promo__text {
  font-size: 16px; }

.promo__item {
  position: relative;
  padding-left: 50px;
  font-size: 16px; }
  .promo__item::before {
    content: "";
    position: absolute;
    top: 2px;
    left: 0;
    width: 22px;
    height: 22px;
    background-image: url("../img/list-icon.svg");
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: cover; }

.promo__stats {
  background: linear-gradient(to right, #211b19, #4e3427); }

.promo__value {
  font-size: 30px;
  font-weight: 200;
  color: #ffffff; }

@media (min-width: 768px) {
