    left: 50%; }
  .promo__row::before {
    top: -10px;
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: cover;
    transform: translateY(-100%) translateX(-50%); }
  .promo__row::after {
    bottom: -15px;
    width: 30px;
    height: 2px;
    background-color: #ffffff;
    transform: translateY(100%) translateX(-50%); }
  .promo__row--client::before {
    width: 44px;
    height: 44px;
    background-image: url("../img/client-icon.svg"); }
  .promo__row--coffee::before {
    width: 48px;
    height: 44px;
    background-image: url("../img/coffe-icon.svg"); }
  .promo__row--post::before {
    width: 44px;
    height: 44px;
    background-image: url("../img/post-icon.svg"); }
  .promo__row--like::before {
    width: 44px;
    height: 44px;
    background-image: url("../img/heart-icon.svg"); }
  .promo__row--launch::before {
    width: 54px;
    height: 44px;
    background-image: url("../img/launch-icon.svg"); }

